import React, { useState, useEffect } from "react";
import { createGlobalStyle } from "styled-components";
import Home from "./Home";
import BentoCakes from "./BentoCakes";
import WholeCakes from "./WholeCakes";
import Footer from "./Footer";
import NavBar from "./NavBar";
import Client from "shopify-buy";

const client = Client.buildClient({
  domain: "riyuecafe.myshopify.com",
  storefrontAccessToken: "f6c9691a7cb1bcf5c67f907599a0230e",
});

const useOnScreen = (options) => {
  const [ref, setRef] = useState(null);
  const [visible, setVisible] = useState(false);

  useEffect(() => {
    // Fetch all products in your shop
    client.product.fetchAll().then((products) => {
      // Do something with the products
      // console.log(products);
    });
  });

  useEffect(() => {
    const observer = new IntersectionObserver(([entry]) => {
      if (entry.target.id === "bentoCakes") {
        setVisible(entry.isIntersecting);
      }

      // console.log(entry);
    }, options);

    if (ref) {
      observer.observe(ref);
    }

    return () => {
      if (ref) {
        observer.unobserve(ref);
      }
    };
  }, [ref, options]);
  return [setRef, visible];
};
const useOnScreen2 = (options) => {
  const [ref2, setRef2] = useState(null);
  const [visible2, setVisible2] = useState(false);

  useEffect(() => {
    const observer = new IntersectionObserver(([entry]) => {
      if (entry.target.id === "wholeCakes") {
        setVisible2(entry.isIntersecting);
        // console.log(entry.isIntersecting);
      }
      // console.log(entry);
    }, options);

    if (ref2) {
      observer.observe(ref2);
    }

    return () => {
      if (ref2) {
        observer.unobserve(ref2);
      }
    };
  }, [ref2, options]);
  return [setRef2, visible2];
};

const App = () => {
  const [setRef, visible] = useOnScreen({
    // root: null,
    // rootMargin: "0px",
    threshold: 1,
  });
  const [setRef2, visible2] = useOnScreen2({
    // root: null,
    // rootMargin: "0px",
    threshold: 1,
  });
  // const [curPage, setCurPage] = useState("home");
  // useEffect(() => {
  //   setCurPage(window.location.href);
  //   console.log(curPage);
  // }, [visible]);

  return (
    <>
      <Globalstyle />
      <div className="container">
        <NavBar />
        <section id="home">
          <Home />
        </section>
        <section ref={setRef} id="bentoCakes">
          <BentoCakes
            visible={visible}
            // curPage={curPage}
            // setCurPage={setCurPage}
          />
        </section>
        <section ref={setRef2} id="wholeCakes">
          <WholeCakes visible={visible2} />
        </section>
        <section id="footer">
          <Footer />
        </section>
      </div>
    </>
  );
};

