const Info = ({ i, visible, type }) => {
  const infos =
    type === "wholeCakes" ? product.wholeCakes.infos : product.bentoCakes.infos;

  const info = infos.map((info, i) => {
    return (
      <div className={`info info${i}`} key={i}>
        <h2>{info.title}</h2>
        <h3>{info.info}</h3>
        <h4>
          <a href="https://riyuecafe.myshopify.com/collections/all">Buy </a>
        </h4>
      </div>
    );
  });
  return (
    <StyledInfo i={i} visible={visible}>
      <div className="infoBox">
        <>{info}</>
        {/* <div id="product-component-1617669929072"></div> */}
      </div>
    </StyledInfo>
  );
};

const InforWrap = ({ i, visible, ...p }) => <div {...p} />;

