import react from "react";
import cakes from "./cakes.png";
import cart from "./cart.png";
import instagram from "./instagram.png";
import facebook from "./facebook.png";
import rabbit from "./rabbit.png";
import styled from "styled-components";

const StyledFooter = styled.div`
  width: 100%;
  min-height: 100vh;
  min-height: -webkit-fill-available;
  /* border: 1px red solid; */
  overflow: hidden;
  position: relative;
  background-blend-mode: multiply;
  background: linear-gradient(105deg, rgba(10, 10, 10, 0) 25%, #fde275 40%);
  .background {
    /* display: none; */
    position: absolute;
    width: 80rem;
    height: 100%;
    background-image: url(${cakes});
    background-repeat: no-repeat;
    background-size: 70%;
    transform: rotate(20deg) translate(-10%, 10%) scale(1.2);
    opacity: 0.3;
    z-index: -10;
    filter: brightness(1.4);
  }
  .order {
    position: absolute;
    width: 50%;
    height: 30%;
    top: 35%;
    left: 50%;
    transform: translate(-55%, -50%);
    /* border: 1px red solid; */
  }
  .order > h1 {
    color: #1a2d59;
    font-family: "Caveat", cursive;
    font-weight: 2;
    font-size: 5rem;
    position: absolute;
    transform: translate(-50%, -50%);
    left: 55%;
    top: 40%;
    transition: 1s;
    animation: orderNow 2s infinite;
    &:hover {
      color: #ccced3;
      animation: none;
    }
    &::after {
      content: "";
      position: absolute;
      transform: scale(0.6);
      top: -90%;
      left: 27%;
      width: 100%;
      height: 100%;
      animation: arrow 1s infinite;
    }
  }

  .shopify > img {
    position: absolute;
    width: 60%;
    left: 50%;
    top: 60%;
    transform: translate(-50%, -50%);
    display: none;

    &:hover {
      transform: rotate(-10deg) translate(-50%, -50%);
      filter: contrast(1);
    }
  }
  .instagram {
    transform: scale(0.8) translate(-50%, -50%);
    position: absolute;
    bottom: 10%;
    left: 49%;
  }
  .facebook {
    transform: scale(0.8) translate(-50%, -50%);
    position: absolute;
    bottom: 10.5%;
    left: 52%;
  }
  .rabbit {
    position: absolute;
    bottom: 20%;
    left: 65%;
    filter: contrast(0.7);
    transition: 1s;
    &:hover {
      filter: contrast(1);
    }
  }

  & > a:nth-child(3) {
    position: absolute;
    font-family: "Caveat", cursive;
    font-size: 2rem;
    left: 61%;
    bottom: 35%;
    transform: translate(-50%, -50%) rotate(-10deg);
    text-decoration: none;
    color: #6f7174;
    /* border: 1px rgba(255, 255, 255, 0.2) solid; */
    padding: 0.4rem;
    border-radius: 30px;
    &:hover {
      transform: translate(-50%, -43%);
    }
    &:hover & ~ a > img {
      transform: rotate(-10deg) translate(-50%, -50%);
    }
  }

  @keyframes orderNow {
    0% {
      color: #1a2d59;
    }
    50% {
      color: #939597;
    }
    100% {
      color: #1a2d59;
    }
  }
  @keyframes arrow {
    50% {
      transform: scale(0.6) translateY(10%);
    }
    100% {
      transform: scale(0.6) translateY(0%);
    }
  }

  @media only screen and (max-width: 1300px) {
    .order {
      width: 30%;
      left: 51%;
    }
    .order > h1 {
      transform: scale(0.8) translate(-50%, -50%);
      left: 50%;
      top: 30%;
    }
    .instagram {
      left: 48%;
    }
    .facebook {
      left: 52%;
    }
  }
  @media only screen and (max-width: 425px) {
    .instagram {
      left: 45%;
      transform: scale(0.6) translate(-50%, -50%);
    }
    .facebook {
      left: 55%;
      transform: scale(0.6) translate(-50%, -50%);
    }
    .rabbit {
      transform: scale(0.6);
    }
    .order > h1 {
      transform: scale(0.8) translate(-50%, -50%);
      left: 50%;
      top: 30%;
      &::after {
        transform: scale(0.6);
        top: -70%;
        left: 20%;
        width: 100%;
        height: 100%;
        animation: arrow 1s infinite;
      }
    }
    & > a:nth-child(3) {
      transform: scale(0.5) translate(-50%, -50%) rotate(-10deg);
      top: 55%;
    }
  }
`;

const Footer = () => {
  return (
    <StyledFooter>
      {/* <img src={cakes} alt="cakes" /> */}
      <a
        className="order"
        href="https://riyuecafe.myshopify.com/"
        target="blank"
      >
        <h1>Order Now</h1>
      </a>
      <div className="background"></div>
      <a href="https://riyuecafe.myshopify.com/" target="blank">
        Enjoy your cakes!!
      </a>
      <a
        className="shopify"
        href="https://riyuecafe.myshopify.com/"
        target="blank"
      >
        <img src={cart} />
      </a>
      <a
        className="instagram"
        href="https://www.instagram.com/riyuecakes/"
        target="blank"
      >
        <img src={instagram} />
      </a>
      <a
        className="facebook"
        href="https://www.facebook.com/Ri.YueCakes"
        target="blank"
      >
        <img src={facebook} />
      </a>
      <img className="rabbit" src={rabbit} />
    </StyledFooter>
  );
};

export default Footer;
