import React from "react";
import styled from "styled-components";
import arrow1 from "./arrow.png";

export const Carousel = ({ images, state, dispatch, visible, type }) => {
  const imagess = images.map((image, i) => (
    <img className={`img-${i}`} src={image} key={image + i}></img>
  ));
  // const dots = images.map((image, i) => <input key={i} type="radio"></input>);

  const ingredients = ["boba", "taro"];

  const ingredient = ingredients.map((ingredient, i) => {
    return (
      <div className={`ig ig${i}`} key={i}>
        <img src={arrow1} />
        <h4>{ingredient}</h4>
      </div>
    );
  });

  return (
    <Slider
      translateX={state.translateX}
      transition={state.transition}
      i={state.i}
      visible={visible}
    >
      {imagess}
      {/* <div className="arrows">
        <i onClick={slideLeft} className="fas fa-chevron-right fa-2x"></i>
        <i onClick={slideRight} className="fas fa-chevron-left fa-2x"></i>
      </div> */}
      {type === "wholeCakes" ? null : (
        <div className="ingredientBox">{ingredient}</div>
      )}
    </Slider>
  );
};

