import styled from "styled-components";
import product from "./product.json";

let names;

const productNav = ({ i, dispatch, visible, type }) => {
  if (type === "bentoCakes") {
    names = product.bentoCakes.names;
  }
  if (type === "wholeCakes") {
    names = product.wholeCakes.names;
  }
  const jumpToImage = (e) => {
    //TODO:
    const target = e.target;
    if (!target.className.includes("click")) return;
    // if () return;
    // console.log(target.className);
    // console.log(target.className.includes("dot dot"));
    dispatch({ type: "jump", payload: +target.id });
  };
  const dots = names.map((name, i) => {
    return (
      <div className={`clickBox click${i}`} id={`${i}`}>
        <div className={`dot dot${i} click`} key={i} id={`${i}`}></div>
        <div className="click123" id={`${i}`}>
          {name}
        </div>
      </div>
    );
  });
  return (
    <StyledProductNav i={i} visible={visible}>
      <div onClick={jumpToImage} className="dotsBox">
        {dots}
      </div>
    </StyledProductNav>
  );
};

const ProductWrap = ({ i, visible, ...p }) => <div {...p} />;

const StyledProductNav = styled(ProductWrap)`
  z-index: 1000;
  grid-column: 2;
  grid-row: 2;
  align-self: start;
  width: auto;
  height: 10rem;
  position: relative;
  top: 2rem;
  transition: 1s;
  opacity: ${(p) => (p.visible ? 1 : 0)};
  /* transform: translateX(-100%);
  transform: translateX(calc(${(p) => (p.visible ? 0 : -100)}*1%)); */
  left: calc(${(p) => (p.visible ? 0 : -100)}*1%);
  .clickBox {
    color: #838585;
    display: flex;
    cursor: pointer;
    /* margin: 0.1rem; */
    box-sizing: border-box;
    /* border: red 1px solid; */
    align-items: center;
    width: 22rem;
    height: 1.5rem;
    transition: 0.3s;
    z-index: 700;
    &:hover {
      font-size: 1.2rem;
      box-sizing: border-box;
      /* margin-top: 0.5rem; */
      color: #838585;
      padding-bottom: 0.5rem;
    }
  }

  .click${(p) => p.i} {
    font-size: 1.4rem;
    color: #4d61a5;
    margin-top: 0.3rem;
    margin-bottom: 0.3rem;
    &:hover {
      font-size: 1.5rem;
    }
    transform: translateX(0.3rem);
  }
  .dotsBox {
    /* border: 1px red solid; */
    position: absolute;
    /* transform: translate(-50%, -50%); */
    /* bottom: -60%;
    left: -75%; */
    display: flex;
    flex-direction: column;
    z-index: 50;
  }
  .dot {
    cursor: pointer;
    margin-left: 0.2rem;
    margin-right: 0.6rem;
    width: 0.5rem;
    height: 0.5rem;
    background-color: rgba(255, 255, 255, 0.486);
    border-radius: 100rem;
    transition: all 0.4s;

    &:hover {
      background-color: white;
    }
  }
  .dot${(p) => p.i} {
    background-color: white;
    border-radius: 100rem;
    height: 1rem;
    width: 0.4rem;
    /* transform: translateX(0.3rem); */
  }
  @media only screen and (min-width: 426px) {
    grid-row: 4;
    left: -50%;
  }

  @media only screen and (max-width: 425px) {
    position: relative;
    grid-column: 5;
    grid-row: 3;
    width: 100%;
    height: 1rem;
    top: -10%;
    left: 30%;
    justify-self: center;
    transform: translate(-50%, -50%);
    /* border: 1px red solid; */

    .click123 {
      display: none;
    }
    .dotsBox {
      flex-direction: row;
    }
    .dot {
      background-color: rgba(51, 99, 238, 0.486);
    }
    .dot${(p) => p.i} {
      background-color: rgba(31, 55, 121, 0.486);
    }
    .clickBox {
      width: auto;
    }
  }
`;

export default productNav;
