import React from "react";
import styled from "styled-components";

const Arrows = ({ i, dispatch, images }) => {
  const slideLeft = (e) => {
    // console.log(e);
    // if (state.i === images.length + 1) return;

    dispatch({ type: "increment" });

    if (i === images.length - 1) {
      setTimeout(() => {
        dispatch({ type: "resetFirst" });
      }, 0);
    }
  };

  const slideRight = (e) => {
    // console.log(e);
    // if (state.i === 0) return;
    dispatch({ type: "decrement" });
    if (i === 0) {
      setTimeout(() => {
        dispatch({ type: "resetLast", payload: images });
      }, 0);
    }
  };
  return (
    <StyledArrows>
      <div className="arrows">
        <i onClick={slideLeft} className="fas fa-chevron-right fa-2x"></i>
        <i onClick={slideRight} className="fas fa-chevron-left fa-2x"></i>
      </div>
    </StyledArrows>
  );
};

const StyledArrows = styled.div`
  position: relative;
  top: 10%;
  align-self: center;
  grid-column: 5 / span 6;
  grid-row: 2;
  z-index: 1000;
  /* border: 1px red solid; */
  .fa-chevron-right {
    color: #fff;
    position: absolute;
    /* top: 47%; */
    right: 15%;
    /* animation: arrows 3s infinite; */
    opacity: 0.7;
    cursor: pointer;
    &:hover {
      color: white;
      transform: translateX(10%);
    }
    z-index: 1000;
  }
  .fa-chevron-left {
    color: #fff;
    position: absolute;
    /* top: 47%; */
    left: 0%;
    /* animation: arrows 3s infinite; */
    opacity: 0.7;
    cursor: pointer;
    &:hover {
      color: white;
      transform: translateX(-10%);
    }
    z-index: 1000;
  }

  .arrows:not(:hover) {
    .fa-chevron-left,
    .fa-chevron-right {
      animation: arrows 3s infinite;
      &:hover {
        animation: none;
      }
    }
  }
  @keyframes arrows {
    0% {
      color: rgba(46, 95, 255, 0.7);
    }
    50% {
      color: rgba(255, 255, 255, 0.3);
    }
    100% {
      color: rgba(46, 95, 255, 0.7);

      /* color: white; */
    }
  }
  @media only screen and (min-width: 426px) {
    grid-column: 4 / span 6;
    grid-row: 4;
    align-self: start;
    left: -5%;
  }
  @media only screen and (max-width: 425px) {
    grid-column: 2 / span 14;
    .fa-chevron-right {
      right: 8%;
    }
  }
`;

export default Arrows;
