import React, { useEffect, useReducer } from "react";
import styled from "styled-components";
import { Carousel } from "./Carousel";
import { PageLayout } from "./PageLayout";
import Info from "./Info";
import img1 from "./img-22.png";
import img2 from "./img-11.png";
import img3 from "./img-33.png";
import img4 from "./img-4.png";
import img5 from "./img-5.png";
import woodTable from "./woodTable.png";
import ProductNav from "./ProductNav";
import Arrows from "./Arrows";

const images = [img1, img2, img3];

const initialState = {
  translateX: "0%",
  transition: "0.4s",
  i: 0,
};

const reducer = (state, action) => {
  switch (action.type) {
    case "increment":
      // console.log(state.i);
      return {
        ...state,
        i: state.i + 1,
        translateX: `${-100 * (state.i + 1)}%`,
        transition: "0.4s",
      };
    case "jump":
      // console.log(state.i);
      return {
        ...state,
        i: action.payload,
        translateX: `${-100 * action.payload}%`,
        transition: "0.4s",
      };
    case "resetFirst":
      return {
        ...state,
        i: 0,
        translateX: "0%",
        // transition: "0",
      };
    case "resetLast":
      return {
        ...state,
        i: action.payload.length - 1,
        translateX: `${-100 * (action.payload.length - 1)}%`,
        // transition: "0",
      };

    case "decrement":
      // console.log(state.i);

      return {
        ...state,
        i: state.i - 1,
        translateX: `${-100 * (state.i - 1)}%`,
        transition: "0.4s",
      };

    default:
      return state;
  }
};

const WholeCakes = ({ visible }) => {
  const [state, dispatch] = useReducer(reducer, initialState);

  // useEffect(() => {}, [visible]);

  return (
    <StyledWholeCakes visible={visible}>
      <PageLayout>
        <h1>Whole Cakes</h1>
        <div className="frontBackground"></div>
        <ProductNav
          i={state.i}
          dispatch={dispatch}
          visible={visible}
          type="wholeCakes"
        />
        <Arrows i={state.i} dispatch={dispatch} images={images} />
        <Carousel
          visible={visible}
          images={images}
          state={state}
          dispatch={dispatch}
          type="wholeCakes"
        />
        <Info i={state.i} visible={visible} type="wholeCakes" />
        <img className="woodTable" src={woodTable} />
        <a className="top" href="#home">
          Top
        </a>
      </PageLayout>
    </StyledWholeCakes>
  );
};

